import React from 'react';
import {
  Box,
  Typography,
  Button,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  IconButton,
  MenuItem,
  Select,
  FormControl,
  InputAdornment
} from '@mui/material';
import { Add as AddIcon, Delete as DeleteIcon, ArrowBack as ArrowBackIcon } from '@mui/icons-material';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { vi } from 'date-fns/locale';

// nh ngha kiu d liu cho t thanh ton
export interface PaymentItem {
  id: string;
  condition: string;
  amount: number;
  paymentDate: Date | null;
  status: 'pending' | 'waiting' | 'paid';
  note: string;
}

interface PaymentScheduleStepProps {
  payments: PaymentItem[];
  onPaymentsChange: (payments: PaymentItem[]) => void;
  onBack: () => void;
  onNext: () => void;
}

const PaymentScheduleStep: React.FC<PaymentScheduleStepProps> = ({
  payments,
  onPaymentsChange,
  onBack,
  onNext
}) => {
  // nh ngha cc trng thi thanh ton
  const paymentStatuses = [
    { value: 'pending', label: 'Cha thanh ton' },
    { value: 'waiting', label: 'Ch thanh ton' },
    { value: 'paid', label: ' thanh ton' }
  ];

  // Hm thm t thanh ton mi
  const handleAddPayment = () => {
    const newPayment: PaymentItem = {
      id: `payment-${Date.now()}`,
      condition: '',
      amount: 0,
      paymentDate: new Date(),
      status: 'pending',
      note: ''
    };
    onPaymentsChange([...payments, newPayment]);
  };

  // Hm xa t thanh ton
  const handleDeletePayment = (id: string) => {
    onPaymentsChange(payments.filter(payment => payment.id !== id));
  };

  // Hm cp nht thng tin t thanh ton
  const handlePaymentChange = (id: string, field: keyof PaymentItem, value: any) => {
    onPaymentsChange(
      payments.map(payment => 
        payment.id === id ? { ...payment, [field]: value } : payment
      )
    );
  };

  // Khng cn hm formatCurrency v  x l trc tip trong TextField

  // Hm x l khi ngi dng nhp s tin
  const handleAmountChange = (id: string, value: string) => {
    // Loi b tt c cc k t khng phi s
    const numericValue = value.replace(/[^0-9]/g, '');
    const amount = numericValue ? parseInt(numericValue, 10) : 0;
    handlePaymentChange(id, 'amount', amount);
  };

  return (
    <Box>
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
        <IconButton onClick={onBack} sx={{ mr: 1 }}>
          <ArrowBackIcon />
        </IconButton>
        <Typography variant="h6">
          Hng mc thanh ton
        </Typography>
      </Box>
      
      <Paper variant="outlined" sx={{ p: 2, mb: 3 }}>
        <Typography variant="body2" color="text.secondary" paragraph>
          Thm cc t thanh ton cho hp ng. Mi t thanh ton bao gm iu kin, s tin, ngy thanh ton, trng thi v ghi ch.
        </Typography>
        
        <TableContainer>
          <Table size="small">
            <TableHead>
              <TableRow>
                <TableCell>iu kin</TableCell>
                <TableCell>S tin</TableCell>
                <TableCell>Ngy thanh ton</TableCell>
                <TableCell>Trng thi</TableCell>
                <TableCell>Ghi ch</TableCell>
                <TableCell>Xa</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {payments.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={6} align="center">
                    <Typography variant="body2" color="text.secondary">
                      Cha c t thanh ton no. Nhn "Thm t thanh ton"  bt u.
                    </Typography>
                  </TableCell>
                </TableRow>
              ) : (
                payments.map((payment) => (
                  <TableRow key={payment.id}>
                    <TableCell>
                      <TextField
                        fullWidth
                        size="small"
                        value={payment.condition}
                        onChange={(e) => handlePaymentChange(payment.id, 'condition', e.target.value)}
                        placeholder="V d: Sau khi k H"
                      />
                    </TableCell>
                    <TableCell>
                      <TextField
                        fullWidth
                        size="small"
                        value={payment.amount === 0 ? '' : payment.amount.toString()}
                        onChange={(e) => handleAmountChange(payment.id, e.target.value)}
                        placeholder="0"
                        InputProps={{
                          endAdornment: <InputAdornment position="end">VN</InputAdornment>,
                        }}
                      />
                    </TableCell>
                    <TableCell>
                      <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={vi}>
                        <DatePicker
                          value={payment.paymentDate}
                          onChange={(date) => handlePaymentChange(payment.id, 'paymentDate', date)}
                          slotProps={{ textField: { size: 'small', fullWidth: true } }}
                        />
                      </LocalizationProvider>
                    </TableCell>
                    <TableCell>
                      <FormControl fullWidth size="small">
                        <Select
                          value={payment.status}
                          onChange={(e) => handlePaymentChange(payment.id, 'status', e.target.value)}
                        >
                          {paymentStatuses.map((status) => (
                            <MenuItem key={status.value} value={status.value}>
                              {status.label}
                            </MenuItem>
                          ))}
                        </Select>
                      </FormControl>
                    </TableCell>
                    <TableCell>
                      <TextField
                        fullWidth
                        size="small"
                        value={payment.note}
                        onChange={(e) => handlePaymentChange(payment.id, 'note', e.target.value)}
                        placeholder="Ghi ch"
                      />
                    </TableCell>
                    <TableCell>
                      <IconButton 
                        color="error" 
                        onClick={() => handleDeletePayment(payment.id)}
                        size="small"
                      >
                        <DeleteIcon />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </TableContainer>
        
        <Box sx={{ mt: 2 }}>
          <Button
            startIcon={<AddIcon />}
            variant="outlined"
            onClick={handleAddPayment}
          >
            Thm t thanh ton
          </Button>
        </Box>
      </Paper>
      
      <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
        <Button 
          variant="outlined" 
          onClick={onBack}
        >
          Quay li
        </Button>
        <Button 
          variant="contained" 
          color="primary"
          onClick={onNext}
        >
          Tip tc
        </Button>
      </Box>
    </Box>
  );
};

export default PaymentScheduleStep;
