import React, { useState } from 'react';
import { useForm, SubmitHandler } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Box,
  Typography,
  CircularProgress,
  Paper,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@mui/material';
import { Close as CloseIcon, CloudUpload as UploadIcon, Delete as DeleteIcon } from '@mui/icons-material';
import { UploadTemplateFormValues, UploadTemplateProps } from './types';

// Define validation schema
const schema = yup.object({
  name: yup.string().required('Tn template l bt buc'),
  description: yup.string().optional(),
  type: yup.string().oneOf(['internal', 'customer']).required('Loi template l bt buc'),
  file: yup.mixed<File>().required('File template l bt buc')
    .test('fileType', 'Ch chp nhn file .docx', (value) => {
      if (!value) return false;
      const file = value as File;
      return file.type === 'application/vnd.openxmlformats-officedocument.wordprocessingml.document';
    }),
  extractedFields: yup.array().of(
    yup.object({
      originalText: yup.string().required(),
      placeholder: yup.string().required(),
      type: yup.string().required(),
      description: yup.string().optional()
    })
  ).default([])
}).required();

interface ExtractedField {
  originalText: string;
  placeholder: string;
  type: string;
  description?: string;
}

const UploadTemplate: React.FC<UploadTemplateProps> = ({ open, onClose, onSubmit }) => {
  const [loading, setLoading] = useState(false);
  const [analyzing, setAnalyzing] = useState(false);
  const [extractedFields, setExtractedFields] = useState<ExtractedField[]>([]);

  type FormData = yup.InferType<typeof schema>;

  const {
    register,
    handleSubmit,
    reset,
    setValue,
    watch,
    formState: { errors },
  } = useForm<FormData>({
    resolver: yupResolver(schema) as any,
    defaultValues: {
      name: '',
      description: '',
      type: 'customer' as const,
      file: undefined,
      extractedFields: [],
    },
  });

  const selectedFile = watch('file');

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      setValue('file', file);
      setAnalyzing(true);
      
      // Gi lp phn tch AI
      setTimeout(() => {
        // Mock d liu c phn tch t file
        const mockExtractedFields: ExtractedField[] = [
          {
            originalText: 'CNG TY TNHH GIY CAO PHT',
            placeholder: 'TEN_CONG_TY_A',
            type: 'text',
            description: 'Tn cng ty bn A'
          },
          {
            originalText: 'Tha t s 352, T bn  s 14, Khu Ph M Hip, Phng Thi Ho, Thnh ph Tn Uyn, Tnh Bnh Dng',
            placeholder: 'DIA_CHI_A',
            type: 'text',
            description: 'a ch bn A'
          },
          {
            originalText: '0274 3775 179',
            placeholder: 'DIEN_THOAI_A',
            type: 'phone',
            description: 'S in thoi bn A'
          },
          {
            originalText: '3701711279',
            placeholder: 'MA_SO_THUE_A',
            type: 'taxcode',
            description: 'M s thu bn A'
          },
          {
            originalText: 'L TH KIM CHI',
            placeholder: 'NGUOI_DAI_DIEN_A',
            type: 'text',
            description: 'Ngi i din bn A'
          },
          {
            originalText: 'Gim c',
            placeholder: 'CHUC_VU_A',
            type: 'text',
            description: 'Chc v ngi i din bn A'
          }
        ];
        
        setExtractedFields(mockExtractedFields);
        setValue('extractedFields', mockExtractedFields);
        setAnalyzing(false);
      }, 2000);
    }
  };
  
  const handleUpdatePlaceholder = (index: number, field: keyof ExtractedField, value: string) => {
    const updatedFields = [...extractedFields];
    updatedFields[index] = { ...updatedFields[index], [field]: value };
    setExtractedFields(updatedFields);
    setValue('extractedFields', updatedFields);
  };

  const handleFormSubmit: SubmitHandler<FormData> = async (data) => {
    try {
      setLoading(true);
      await onSubmit(data as UploadTemplateFormValues);
      handleClose();
    } catch (error) {
      console.error('Error uploading template:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleClose = () => {
    reset();
    setExtractedFields([]);
    onClose();
  };

  return (
    <Dialog open={open} onClose={handleClose} maxWidth="lg" fullWidth>
      <DialogTitle>
        <Box display="flex" justifyContent="space-between" alignItems="center">
          <Typography variant="h6">Ti ln Template mi</Typography>
          <IconButton onClick={handleClose} size="small">
            <CloseIcon />
          </IconButton>
        </Box>
      </DialogTitle>
      <form onSubmit={handleSubmit(handleFormSubmit as any)}>
        <DialogContent dividers>
          <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: '1fr 2fr' }, gap: 3 }}>
            <Box>
              <TextField
                {...register('name')}
                label="Tn template"
                fullWidth
                margin="normal"
                error={!!errors.name}
                helperText={errors.name?.message}
                disabled={loading}
              />
              
              <TextField
                {...register('description')}
                label="M t"
                fullWidth
                multiline
                rows={3}
                margin="normal"
                disabled={loading}
              />
              
              <FormControl fullWidth margin="normal" error={!!errors.type}>
                <InputLabel>Loi template</InputLabel>
                <Select
                  label="Loi template"
                  defaultValue="customer"
                  disabled={loading}
                  {...register('type')}
                >
                  <MenuItem value="customer">Cho khch hng</MenuItem>
                  <MenuItem value="internal">Ni b</MenuItem>
                </Select>
              </FormControl>
              
              <Box mt={2} mb={2}>
                <input
                  accept=".docx,application/vnd.openxmlformats-officedocument.wordprocessingml.document"
                  style={{ display: 'none' }}
                  id="template-upload"
                  type="file"
                  onChange={handleFileChange}
                  disabled={loading || analyzing}
                />
                <label htmlFor="template-upload">
                  <Button
                    variant="outlined"
                    component="span"
                    startIcon={<UploadIcon />}
                    disabled={loading || analyzing}
                  >
                    Chn file template (.docx)
                  </Button>
                </label>
                {selectedFile && (
                  <Typography variant="body2" sx={{ mt: 1 }}>
                     chn: {selectedFile.name}
                  </Typography>
                )}
                {errors.file && (
                  <Typography color="error" variant="caption" display="block">
                    {errors.file.message as React.ReactNode}
                  </Typography>
                )}
              </Box>
            </Box>
            
            <Box>
              <Paper variant="outlined" sx={{ p: 2, height: '100%' }}>
                <Typography variant="subtitle1" gutterBottom>
                  Cc trng c phn tch t hp ng:
                </Typography>
                
                {analyzing ? (
                  <Box display="flex" flexDirection="column" alignItems="center" justifyContent="center" py={4}>
                    <CircularProgress size={40} />
                    <Typography variant="body2" color="textSecondary" sx={{ mt: 2 }}>
                      ang phn tch ni dung hp ng...
                    </Typography>
                  </Box>
                ) : extractedFields.length > 0 ? (
                  <TableContainer component={Paper} variant="outlined">
                    <Table size="small">
                      <TableHead>
                        <TableRow>
                          <TableCell>Trng gc trong file</TableCell>
                          <TableCell>Placeholder gi </TableCell>
                          <TableCell>Kiu d liu</TableCell>
                          <TableCell>M t</TableCell>
                          <TableCell align="center">Thao tc</TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>
                        {extractedFields.map((field, index) => (
                          <TableRow key={index}>
                            <TableCell>{field.originalText}</TableCell>
                            <TableCell>
                              <TextField
                                size="small"
                                value={field.placeholder}
                                onChange={(e) => handleUpdatePlaceholder(index, 'placeholder', e.target.value)}
                                fullWidth
                                disabled={loading}
                              />
                            </TableCell>
                            <TableCell>
                              <FormControl fullWidth size="small">
                                <Select
                                  value={field.type}
                                  onChange={(e) => handleUpdatePlaceholder(index, 'type', e.target.value)}
                                  disabled={loading}
                                >
                                  <MenuItem value="text">Vn bn</MenuItem>
                                  <MenuItem value="number">S</MenuItem>
                                  <MenuItem value="date">Ngy thng</MenuItem>
                                  <MenuItem value="phone">in thoi</MenuItem>
                                  <MenuItem value="email">Email</MenuItem>
                                  <MenuItem value="taxcode">M s thu</MenuItem>
                                </Select>
                              </FormControl>
                            </TableCell>
                            <TableCell>
                              <TextField
                                size="small"
                                value={field.description || ''}
                                onChange={(e) => handleUpdatePlaceholder(index, 'description', e.target.value)}
                                fullWidth
                                disabled={loading}
                              />
                            </TableCell>
                            <TableCell align="center">
                              <IconButton size="small" color="error" onClick={() => {
                                const updatedFields = [...extractedFields];
                                updatedFields.splice(index, 1);
                                setExtractedFields(updatedFields);
                                setValue('extractedFields', updatedFields);
                              }} disabled={loading}>
                                <DeleteIcon fontSize="small" />
                              </IconButton>
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </TableContainer>
                ) : selectedFile ? (
                  <Typography variant="body2" color="textSecondary">
                    Khng tm thy trng no c th trch xut t file. Vui lng kim tra li ni dung file.
                  </Typography>
                ) : (
                  <Typography variant="body2" color="textSecondary">
                    Vui lng ti ln file hp ng  AI phn tch v  xut cc trng d liu.
                  </Typography>
                )}
                
                <Box mt={3}>
                  <Typography variant="subtitle2" gutterBottom>
                    Hng dn s dng:
                  </Typography>
                  <Typography variant="body2" color="textSecondary">
                    - AI s t ng phn tch ni dung hp ng v  xut cc trng cn in
                    <br />
                    - Bn c th chnh sa tn placeholder v m t cho ph hp
                    <br />
                    - Khi to hp ng, h thng s t ng in cc thng tin vo cc v tr tng ng
                  </Typography>
                </Box>
              </Paper>
            </Box>
          </Box>
        </DialogContent>
        
        <DialogActions sx={{ p: 2, borderTop: 1, borderColor: 'divider' }}>
          <Button onClick={handleClose} disabled={loading}>
            Hy
          </Button>
          <Button
            type="submit"
            variant="contained"
            color="primary"
            disabled={loading || analyzing || extractedFields.length === 0}
            startIcon={loading ? <CircularProgress size={20} /> : null}
          >
            {loading ? 'ang ti ln...' : 'Lu Template'}
          </Button>
        </DialogActions>
      </form>
    </Dialog>
  );
};

export default UploadTemplate;
