import React from 'react';
import { 
  Box, 
  Typography, 
  Paper, 
  Button, 
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Divider,
  Chip
} from '@mui/material';
import { ArrowBack, Download, Print, Share, Delete, Edit } from '@mui/icons-material';

interface ContractDetailProps {
  contract: {
    id: string;
    name: string;
    templateName: string;
    status: 'draft' | 'pending' | 'signed' | 'expired' | 'cancelled';
    partyA: string;
    partyB: string;
    createdAt: string;
    updatedAt: string;
    createdBy: string;
    signedAt?: string;
    expiresAt?: string;
    contractData?: {
      tenBenA: string;
      tenBenB: string;
      ngayKy: string;
      diaChiA: string;
      dienThoaiA: string;
      maSoThueA: string;
      nguoiDaiDienA: string;
      chucVuA: string;
      giaTriHopDong: string;
    };
  };
  onClose: () => void;
  onDelete: (id: string) => void;
  onEdit: (id: string) => void;
}

const ContractDetail: React.FC<ContractDetailProps> = ({ 
  contract, 
  onClose, 
  onDelete,
  onEdit
}) => {
  const [deleteConfirmOpen, setDeleteConfirmOpen] = React.useState(false);

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'signed': return 'success';
      case 'pending': return 'warning';
      case 'draft': return 'default';
      case 'expired': return 'error';
      case 'cancelled': return 'error';
      default: return 'default';
    }
  };

  const formatDate = (dateString: string) => {
    if (!dateString) return 'Cha xc nh';
    return new Date(dateString).toLocaleDateString('vi-VN');
  };

  return (
    <Box>
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 3, gap: 2 }}>
        <IconButton onClick={onClose}>
          <ArrowBack />
        </IconButton>
        <Typography variant="h5">
          Chi tit hp ng: {contract.name}
        </Typography>
        <Chip 
          label={contract.status.toUpperCase()} 
          color={getStatusColor(contract.status) as any}
          size="small"
          sx={{ ml: 2 }}
        />
      </Box>

      <Paper sx={{ p: 3, mb: 3 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 3, flexWrap: 'wrap', gap: 2 }}>
          <Box>
            <Typography variant="h6" gutterBottom>Thng tin chung</Typography>
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 2 }}>
              <Box sx={{ flex: '1 1 300px' }}>
                <Typography><strong>M hp ng:</strong> {contract.id}</Typography>
                <Typography><strong>Mu hp ng:</strong> {contract.templateName}</Typography>
                <Typography><strong>Ngy to:</strong> {formatDate(contract.createdAt)}</Typography>
              </Box>
              <Box sx={{ flex: '1 1 300px' }}>
                <Typography><strong>Ngi to:</strong> {contract.createdBy}</Typography>
                <Typography><strong>Cp nht ln cui:</strong> {formatDate(contract.updatedAt)}</Typography>
                {contract.signedAt && (
                  <Typography><strong>Ngy k:</strong> {formatDate(contract.signedAt)}</Typography>
                )}
                {contract.expiresAt && (
                  <Typography><strong>Ngy ht hn:</strong> {formatDate(contract.expiresAt)}</Typography>
                )}
              </Box>
            </Box>
          </Box>
          
          <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap' }}>
            <Button variant="outlined" startIcon={<Download />} size="small">
              Ti xung
            </Button>
            <Button variant="outlined" startIcon={<Print />} size="small">
              In
            </Button>
            <Button variant="outlined" startIcon={<Share />} size="small">
              Chia s
            </Button>
            <Button 
              variant="contained" 
              color="primary" 
              startIcon={<Edit />}
              onClick={() => onEdit(contract.id)}
              size="small"
            >
              Chnh sa
            </Button>
            <Button 
              variant="outlined" 
              color="error" 
              startIcon={<Delete />}
              onClick={() => setDeleteConfirmOpen(true)}
              size="small"
            >
              Xa
            </Button>
          </Box>
        </Box>

        <Divider sx={{ my: 3 }} />

        <Box sx={{ mb: 4 }}>
          <Typography variant="h6" gutterBottom>Thng tin cc bn</Typography>
          <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 4 }}>
            <Box sx={{ flex: '1 1 300px' }}>
              <Paper variant="outlined" sx={{ p: 2, height: '100%' }}>
                <Typography variant="subtitle1" fontWeight="bold" gutterBottom>
                  Bn A (Bn giao kt hp ng)
                </Typography>
                {contract.contractData ? (
                  <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1 }}>
                    <Typography><strong>Tn cng ty:</strong> {contract.contractData.tenBenA}</Typography>
                    <Typography><strong>a ch:</strong> {contract.contractData.diaChiA}</Typography>
                    <Typography><strong>in thoi:</strong> {contract.contractData.dienThoaiA}</Typography>
                    <Typography><strong>M s thu:</strong> {contract.contractData.maSoThueA}</Typography>
                    <Typography><strong>Ngi i din:</strong> {contract.contractData.nguoiDaiDienA}</Typography>
                    <Typography><strong>Chc v:</strong> {contract.contractData.chucVuA}</Typography>
                  </Box>
                ) : (
                  <Typography>Khng c thng tin chi tit</Typography>
                )}
              </Paper>
            </Box>
            <Box sx={{ flex: '1 1 300px' }}>
              <Paper variant="outlined" sx={{ p: 2, height: '100%' }}>
                <Typography variant="subtitle1" fontWeight="bold" gutterBottom>
                  Bn B (i tc)
                </Typography>
                {contract.contractData ? (
                  <Typography><strong>Tn cng ty:</strong> {contract.contractData.tenBenB}</Typography>
                ) : (
                  <Typography>Khng c thng tin chi tit</Typography>
                )}
              </Paper>
            </Box>
          </Box>
        </Box>

        <Box>
          <Typography variant="h6" gutterBottom>Ni dung hp ng</Typography>
          <Paper variant="outlined" sx={{ p: 3 }}>
            <Typography variant="h5" align="center" gutterBottom>
              HP NG DCH V
            </Typography>
            <Typography paragraph align="center">
              S: {contract.id}
            </Typography>
            
            <Typography paragraph align="center">
              <em>Cn c B lut Dn s s 91/2015/QH13 ngy 24 thng 11 nm 2015 ca Quc hi</em><br />
              <em>Cn c Lut Thng mi s 36/2005/QH11 ngy 14 thng 6 nm 2005 ca Quc hi</em>
            </Typography>
            
            <Typography paragraph align="center">
              <strong>Hm nay, ngy {contract.contractData?.ngayKy || formatDate(contract.createdAt)}</strong>
            </Typography>
            
            <Typography paragraph>
              <strong>BN A:</strong> {contract.contractData?.tenBenA || contract.partyA}
            </Typography>
            
            <Typography paragraph>
              <strong>BN B:</strong> {contract.contractData?.tenBenB || contract.partyB}
            </Typography>
            
            <Typography paragraph>
              Hai bn cng tha thun k kt Hp ng dch v vi nhng ni dung v iu khon sau y:
            </Typography>
            
            <Typography variant="h6" gutterBottom>IU 1: NI DUNG CNG VIC</Typography>
            <Typography paragraph>
              Bn B ng  cung cp dch v theo yu cu ca Bn A vi ni dung chi tit nh trong Ph lc nh km.
            </Typography>
            
            <Typography variant="h6" gutterBottom>IU 2: THI HN HP NG</Typography>
            <Typography paragraph>
              Hp ng c hiu lc t ngy k n ngy {contract.expiresAt ? formatDate(contract.expiresAt) : 'khi hon thnh ngha v hp ng'}.
            </Typography>
            
            <Typography variant="h6" gutterBottom>IU 3: GI TR HP NG</Typography>
            <Typography paragraph>
              Tng gi tr hp ng: {contract.contractData?.giaTriHopDong || 'Theo tha thun gia hai bn'}
            </Typography>
            
            <Box sx={{ mt: 6, display: 'flex', justifyContent: 'space-between', flexWrap: 'wrap', gap: 2 }}>
              <Box sx={{ textAlign: 'center', minWidth: 200, flex: 1 }}>
                <Typography fontWeight="bold">I DIN BN A</Typography>
                <Typography fontStyle="italic">(K, ghi r h tn)</Typography>
                <Box sx={{ height: 80, mt: 2 }}></Box>
                <Typography fontWeight="bold">{contract.contractData?.nguoiDaiDienA || 'Ngi i din'}</Typography>
                <Typography>{contract.contractData?.chucVuA || 'Chc v'}</Typography>
              </Box>
              
              <Box sx={{ textAlign: 'center', minWidth: 200, flex: 1 }}>
                <Typography fontWeight="bold">I DIN BN B</Typography>
                <Typography fontStyle="italic">(K, ghi r h tn)</Typography>
                <Box sx={{ height: 80, mt: 2 }}></Box>
                <Typography fontWeight="bold">Ngi i din</Typography>
                <Typography>Chc v</Typography>
              </Box>
            </Box>
          </Paper>
        </Box>
      </Paper>

      {/* Delete confirmation dialog */}
      <Dialog open={deleteConfirmOpen} onClose={() => setDeleteConfirmOpen(false)}>
        <DialogTitle>Xc nhn xa hp ng</DialogTitle>
        <DialogContent>
          <Typography>Bn c chc chn mun xa hp ng ny? Hnh ng ny khng th hon tc.</Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteConfirmOpen(false)}>Hy</Button>
          <Button 
            onClick={() => {
              onDelete(contract.id);
              setDeleteConfirmOpen(false);
            }} 
            color="error"
            variant="contained"
          >
            Xc nhn xa
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default ContractDetail;
